import React, { Component, Fragment } from "react";
import ptvapi from "../api/ptvapi";
import { Segment, Placeholder, Header, Form, Button } from "semantic-ui-react";

import ListComponent from "./ListComponent";

class Stop extends Component {
  state = { stop_id: 0, timestamp: "00:00", searching: null, output: {} };
  handleClick = event => {
    this.setState({ searching: true });
    ptvapi
      .get(
        `/api/next_vehicle?stop_id=${this.state.stop_id}&timestamp=${
          this.state.timestamp
        }:00`
      )
      .then(resp => {
        return resp.data.next_line;
      })
      .then(json => {
        this.setState({ searching: false, output: json });
      })
      .catch(err => {
        this.setState({ searching: false });
      });
  };
  handleStopIDChange = event => this.setState({ stop_id: event.target.value });
  handleTimeChange = event => {
    this.setState({ timestamp: event.target.value });
  };
  componentWillMount() {
    let date = new Date();
    let hours = date.getHours();
    if (hours < 10) hours = "0" + hours;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;
    this.setState({ timestamp: `${hours}:${minutes}` });
  }
  render() {
    return (
      <div className="main">
        <Header as="h2" textAlign="center">
          Stop
        </Header>
        <Form>
          <Form.Group widths="equal">
            <Form.Input
              fluid
              label="Stop ID"
              placeholder="0"
              type="number"
              value={this.state.stop_id}
              onChange={this.handleStopIDChange}
              required
            />
            <Form.Input
              fluid
              label="Time"
              placeholder="HH:MM"
              type="time"
              value={this.state.timestamp}
              onChange={this.handleTimeChange}
              required
            />
          </Form.Group>
          <Button type="button" onClick={this.handleClick}>
            Search
          </Button>
        </Form>
        <Fragment>
          {this.state.searching === null ? null : this.state.searching ===
            true ? (
            <Placeholder>
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
            </Placeholder>
          ) : (
            <Segment>
              <ListComponent lines={this.state.output} />
            </Segment>
          )}
        </Fragment>
      </div>
    );
  }
}

export default Stop;
