import React, { Component } from "react";
import { Card, Label } from "semantic-ui-react";
import ptvapi from "../api/ptvapi";

class ListComponent extends Component {
  render() {
    const { lines } = this.props;
    let listItems;
    if (lines === null || lines.length === 0) {
      listItems = (
        <CardComponent
          line_name="No available service"
          line_id=""
          line_time=""
          stop_id=""
        />
      );
    } else {
      listItems = lines.map((line, index) => (
        <CardComponent
          key={index}
          line_name={line.line_name}
          line_id={line.line_id}
          line_time={line.stops[0].stop_time}
          stop_id={line.stops[0].stop_id}
        />
      ));
    }

    return <Card.Group>{listItems}</Card.Group>;
  }
}

class CardComponent extends Component {
  state = { delay: 0, result: null };
  componentWillMount() {
    if (this.props.line_id !== "")
      ptvapi
        .get(`/api/line_delay?line_id=${this.props.line_id}`)
        .then(resp => {
          return resp.data;
        })
        .then(json => {
          this.setState(json);
        })
        .catch(err => {
          console.error(err);
        });
  }
  render() {
    return (
      <Card>
        <Card.Content>
          <Card.Header>
            {this.props.line_name}
            {this.state.result ? (
              <Label size="mini" color="red">
                {`Delayed +${this.state.delay} mins`}
              </Label>
            ) : null}
          </Card.Header>
          <Card.Description>
            {this.props.stop_id !== ""
              ? `Next vehicle to arrive at stop ${
                  this.props.stop_id
                } will be at ${this.props.line_time}`
              : ""}
          </Card.Description>
        </Card.Content>
      </Card>
    );
  }
}

export default ListComponent;
