import React, { Component, Fragment } from "react";
import ptvapi from "../api/ptvapi";
import { Segment, Placeholder, Header, Form, Button } from "semantic-ui-react";

import ListComponent from "./ListComponent";

class Location extends Component {
  state = { x: 0, y: 0, timestamp: "00:00", searching: null, output: {} };
  handleClick = event => {
    this.setState({ searching: true });
    ptvapi
      .get(
        `/api/find_vehicle?x=${this.state.x}&y=${this.state.y}&timestamp=${
          this.state.timestamp
        }:00`
      )
      .then(resp => {
        return resp.data.next_line;
      })
      .then(json => {
        this.setState({ searching: false, output: json });
      })
      .catch(err => {
        this.setState({ searching: false });
      });
  };
  handleXChange = event => this.setState({ x: event.target.value });
  handleYChange = event => this.setState({ y: event.target.value });
  handleTimeChange = event => {
    this.setState({ timestamp: event.target.value });
  };
  componentWillMount() {
    let date = new Date();
    let hours = date.getHours();
    if (hours < 10) hours = "0" + hours;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;
    this.setState({ timestamp: `${hours}:${minutes}` });
  }
  render() {
    return (
      <div className="main">
        <Header as="h2" textAlign="center">
          Location
        </Header>
        <Form>
          <Form.Group widths="equal">
            <Form.Input
              fluid
              label="X coordinate"
              placeholder="0"
              type="number"
              value={this.state.x}
              onChange={this.handleXChange}
              required
            />
            <Form.Input
              fluid
              label="Y coordinate"
              placeholder="0"
              type="number"
              value={this.state.y}
              onChange={this.handleYChange}
              required
            />
            <Form.Input
              fluid
              label="Time"
              placeholder="HH:MM"
              type="time"
              value={this.state.timestamp}
              onChange={this.handleTimeChange}
              required
            />
          </Form.Group>
          <Button type="button" onClick={this.handleClick}>
            Search
          </Button>
        </Form>
        <Fragment>
          {this.state.searching === null ? null : this.state.searching ===
            true ? (
            <Placeholder>
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
              <Placeholder.Line />
            </Placeholder>
          ) : (
            <Segment>
              <ListComponent lines={this.state.output} />
            </Segment>
          )}
        </Fragment>
      </div>
    );
  }
}

export default Location;
